/*
biomebot main worker
==================================

main worker



# 

## 1. 

providerbotIdsyncCache()
indexedDBbotModules
botIdmain worker, part workers

## 2 provider -> worker {type: 'deploy'}

tfidf{type:'deployed'}

## 3. worker -> provider {type: 'deployed'}

providerpartdeployed
deployedavatar
partdeployed4

## 4. provider -> main {type: 'run', summon:summon}
main summon{ON_SUMMON}{ON_START}
part{type:'activate', part:'partName'}


## 5. provider -> main worker {type: 'input', message:message}


*/
import {main} from './main.core';

self.onmessage = (event) => {
  const action = event.data;
  // console.log('mainWorker recieved', action);
  switch (action.type) {
    case 'deploy': {
      main.deploy({...action, worker: self}).then((result) => {
        self.postMessage({
          type: 'deployed',
          startingPart: result.startingPart,
          botRepr: result.botRepr,
        });
      });
      break;
    }

    case 'run': {
      main.run(action);
      break;
    }

    case 'input': {
      main.recieve(action);
      break;
    }

    case 'kill': {
      main.kill();
      self.close();
      break;
    }

    default:
      throw new Error(`mainWorker: invalid action ${action.type}`);
  }
};
